"use client";

import Image from "next/image";
import { useState, useRef, useEffect } from "react";
import { FaSearch, FaUser, FaStar, FaShoppingCart } from "react-icons/fa";

const links = ["Men", "Women", "Essentials", "LookBooks"];

export default function Navbar() {
  const [activeLink, setActiveLink] = useState("Men");
  const [hoverLink, setHoverLink] = useState<string | null>(null);
  const linkRefs = useRef<HTMLAnchorElement[]>([]);
  const [barStyle, setBarStyle] = useState({ left: 0, width: 0 });

  // Update moving bar position
  useEffect(() => {
    const target = hoverLink || activeLink;
    const index = links.indexOf(target);
    const el = linkRefs.current[index];
    if (el) {
      setBarStyle({ left: el.offsetLeft, width: el.offsetWidth });
    }
  }, [activeLink, hoverLink]);

  return (
    <nav className="w-full bg-white relative">
      <div className="flex items-center justify-between px-2 md:px-6 py-4 relative">

        {/* Left Links */}
        <div className="flex space-x-4 md:space-x-6 relative">
          {links.map((link, idx) => (
            <a
              key={link}
              href="#"
              ref={(el) => {
                if (el) linkRefs.current[idx] = el;
              }}
              onClick={() => setActiveLink(link)}
              onMouseEnter={() => setHoverLink(link)}
              onMouseLeave={() => setHoverLink(null)}
              className="relative inline-block text-black text-sm py-2 cursor-pointer"
            >
              {link}
            </a>
          ))}

          {/* Moving border */}
          <span
            className="absolute bottom-0 h-0.5 bg-black rounded-full transition-all duration-300"
            style={{ left: barStyle.left, width: barStyle.width }}
          />
        </div>

        {/* Brand Name */}
        <div className="font-extrabold italic text-2xl text-center text-black">
          PEGADOR
        </div>

        {/* Right Icons */}
        <div className="flex items-center space-x-3 md:space-x-6 text-black">
          <FaSearch className="text-4xl cursor-pointer p-2 hover:bg-gray-100 rounded-full transition" />
          <div className="w-9 h-9 relative rounded-full overflow-hidden">
            <Image
              src="/Pakistan.webp"
              alt="Pakistan"
              fill
              className="object-cover"
            />
          </div>
          <FaUser className="text-4xl cursor-pointer p-2 hover:bg-gray-100 rounded-full transition" />
          <FaStar className="text-4xl cursor-pointer p-2 hover:bg-gray-100 rounded-full transition" />
          <FaShoppingCart className="text-4xl cursor-pointer p-2 hover:bg-gray-100 rounded-full transition" />
        </div>

      </div>
    </nav>
  );
}
